'use client';

import { useState } from 'react';
import { supabase } from '@/lib/supabase';
import { ArrowRight, CheckCircle, AlertCircle, Database } from 'lucide-react';

export default function AdminPage() {
  const [jsonInput, setJsonInput] = useState('');
  const [status, setStatus] = useState<string>('idle');
  const [logs, setLogs] = useState<string[]>([]);

  const addLog = (msg: string) => setLogs((prev) => [...prev, `> ${msg}`]);

  const handleSmartPaste = async () => {
    setStatus('processing');
    setLogs([]);
    addLog('Parsing JSON payload...');

    try {
      // 1. Parse Data
      const data = JSON.parse(jsonInput);
      const { meta, predictions } = data;

      if (!meta || !predictions) throw new Error('Invalid JSON structure');
      
      addLog(`Detected Cycle: ${meta.cycle_id}`);
      addLog(`Platform: ${meta.platform} (${meta.event_name})`);
      addLog(`Predictions found: ${predictions.length}`);

      // 2. Upsert Cycle
      const { error: cycleError } = await supabase
        .from('cycles')
        .upsert({ 
          id: meta.cycle_id, 
          name: 'Auto-Generated Cycle', 
          type: 'Mid', 
          target_goal: 'Hit Target' 
        }, { onConflict: 'id' })
        .select();

      if (cycleError) throw cycleError;
      addLog('Cycle verified/created.');

      // 3. Create Event
      const { data: eventData, error: eventError } = await supabase
        .from('events')
        .insert({
          cycle_id: meta.cycle_id,
          platform: meta.platform,
          event_name: meta.event_name,
          deadline: meta.deadline,
        })
        .select()
        .single();

      if (eventError) throw eventError;
      addLog(`Event created with ID: ${eventData.id}`);

      // 4. Insert Predictions
      const formattedPredictions = predictions.map((p: any) => ({
        event_id: eventData.id,
        match_id: p.id,
        home_team: p.home,
        away_team: p.away,
        match_time: p.time,
        strat_a_pick: p.strat_a,
        strat_b_pick: p.strat_b,
        rationale: p.rationale,
        is_free: p.is_free,
      }));

      const { error: predError } = await supabase
        .from('predictions')
        .insert(formattedPredictions);

      if (predError) throw predError;
      
      addLog('SUCCESS: All predictions inserted.');
      setStatus('success');
      setJsonInput(''); 

    } catch (err: any) {
      // Fixed: Commented out to prevent linting errors
      // console.error(err); 
      addLog(`ERROR: ${err.message}`);
      setStatus('error');
    }
  };

  return (
    <div className="min-h-screen bg-black text-white p-8 font-mono">
      <div className="max-w-4xl mx-auto space-y-8">
        <div className="border-b border-gray-800 pb-4">
          <h1 className="text-3xl font-bold text-cyan-500 flex items-center">
            <Database className="mr-3" /> ADMIN_CONSOLE_v1
          </h1>
          <p className="text-gray-500">The Wizard of Oz Protocol</p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {/* LEFT: INPUT */}
          <div className="space-y-4">
            <label className="text-sm font-bold text-gray-400">SMART_PASTE_TERMINAL</label>
            <textarea
              className="w-full h-[500px] bg-gray-900 border border-gray-700 rounded-lg p-4 text-xs text-green-400 focus:outline-none focus:border-cyan-500 font-mono"
              placeholder="// Paste AI JSON Payload here..."
              value={jsonInput}
              onChange={(e) => setJsonInput(e.target.value)}
            />
            <button
              onClick={handleSmartPaste}
              disabled={status === 'processing'}
              className="w-full py-4 bg-cyan-600 hover:bg-cyan-500 disabled:bg-gray-700 text-black font-bold rounded flex justify-center items-center transition-all"
            >
              {status === 'processing' ? 'PROCESSING...' : 'EXECUTE INJECTION'}
              <ArrowRight className="ml-2 w-4 h-4" />
            </button>
          </div>

          {/* RIGHT: LOGS */}
          <div className="bg-neutral-900 rounded-lg border border-gray-800 p-4 overflow-hidden flex flex-col">
             <div className="flex items-center justify-between mb-4 border-b border-gray-800 pb-2">
               <span className="text-xs font-bold text-gray-500">SYSTEM LOGS</span>
               {status === 'success' && <CheckCircle className="text-green-500 w-4 h-4"/>}
               {status === 'error' && <AlertCircle className="text-red-500 w-4 h-4"/>}
             </div>
             <div className="flex-1 overflow-y-auto space-y-2 font-mono text-xs">
               {logs.length === 0 && <span className="text-gray-700 italic">Waiting for input...</span>}
               {logs.map((log, i) => (
                 <div key={i} className="text-gray-300 border-l-2 border-cyan-900 pl-2">
                   {log}
                 </div>
               ))}
             </div>
          </div>
        </div>
      </div>
    </div>
  );
}